a {
    text-decoration: none;
    color: black;
}

.admin-menu {
    display: flex;
}

.admin-menu #title {
    display: flex;
    align-items: center;
    padding-left: 12%;
}

.image {
    border: 1px solid lightgrey;
}

.menu-item {
    padding-left: 10%;
    padding-top: 10px;
    padding-bottom: 10px;
    border-top: 1px solid lightgrey;
}

.option-container {
    margin-left: 240px;
    margin-right: 30px;
}

.clickable {
    cursor: pointer;
    background-color: white;
    transition: background-color .1s;
}

.clickable:hover {
    background-color: #f0db4f;
}

a:hover {
    color: black;
}