img {
    width: 100%;
    max-width: 150px;
    border: 1px;
    border-radius: 10px;
}

p {
    text-align: center;
}

.description {
    padding-top: 15px;
    height: 80px;
}

.grid-container {
    padding: 40px;
    border-radius: 10px;
}

.content-container {
    display: flex;
    justify-content: center;
}

.button-container {
    display: flex;
    flex-direction: column;
    margin: 20px;
    padding-left: 4px;
}

.image-container {
    display: flex;
    justify-content: center;
}

.column-item {
    position: relative !important;
    top: 0 !important;
    border-radius: 15px !important;
    min-width: 190px;
    max-height: 400px !important;
    color: white;
    transition: background 1s, top 0.3s, color 1s !important;
    cursor: pointer;
}

.column-item:hover {
    background: rgb(240,219,79,0.7) !important;
    top: -10px !important;
    color: black !important;
}

@media only screen and (max-width: 767px) {
    .ui.grid>.stackable.stackable.row>.column, .ui.stackable.grid>.column.grid>.column, .ui.stackable.grid>.column.row>.column, .ui.stackable.grid>.column:not(.row), .ui.stackable.grid>.row>.column, .ui.stackable.grid>.row>.wide.column, .ui.stackable.grid>.wide.column {
        margin: 15px !important;
    }
}